<template>
    <div class="m-simple-pet is-disabled">
        <a
            v-for="item in luckyList"
            :key="item.Index"
            class="u-pet-item"
            :class="item.isDone && 'is-done'"
            :href="getPetLink(item.Index)"
            target="_blank"
        >
            <div class="u-pet-title">
                <div class="u-pet-name">
                    <itemIcon
                        :item_id="String(item.ItemTabType + '_' + item.ItemTabIndex)"
                        :size="38"
                        :onlyIcon="true"
                        :isLink="false"
                    ></itemIcon
                    >{{ item.Name }}
                </div>
                <div class="u-pet-desc">{{ item.szTip }}</div>
            </div>
            <!-- <div class="u-pet-detail">
                <div class="u-times">
                    <div class="u-time" :class="index <= ~~item.name && 'is-active'" v-for="index in 3" :key="index">
                        {{ index }}
                    </div>
                </div>
                <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:class="'u-done-icon'" v-bind:svg-inline="''" :fill="item.isDone ? '#fff' : '#949494'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M8.6 11.8L6.425 9.625a.918.918 0 00-.675-.275c-.267 0-.5.1-.7.3a.948.948 0 00-.275.7c0 .283.092.517.275.7L7.9 13.9a.948.948 0 00.7.275.948.948 0 00.7-.275l5.675-5.675a.918.918 0 00.275-.675c0-.267-.1-.5-.3-.7a.948.948 0 00-.7-.275.948.948 0 00-.7.275L8.6 11.8zM10 20a9.733 9.733 0 01-3.9-.788 10.092 10.092 0 01-3.175-2.137c-.9-.9-1.612-1.958-2.137-3.175A9.732 9.732 0 010 10c0-1.383.263-2.683.788-3.9a10.092 10.092 0 012.137-3.175c.9-.9 1.958-1.613 3.175-2.138A9.743 9.743 0 0110 0c1.383 0 2.683.262 3.9.787a10.105 10.105 0 013.175 2.138c.9.9 1.612 1.958 2.137 3.175A9.733 9.733 0 0120 10a9.733 9.733 0 01-.788 3.9 10.092 10.092 0 01-2.137 3.175c-.9.9-1.958 1.612-3.175 2.137A9.733 9.733 0 0110 20z" fill="tranparent"/></svg>
            </div> -->
        </a>
    </div>
</template>

<script>
import { getPets, getPetLucky } from "@/service/gonggao";

import dayjs from "@/plugins/day";
import itemIcon from "@/components/common/item_icon.vue";
export default {
    name: "SimplePet",
    components: {
        itemIcon,
    },
    props: {
        date: {
            type: String,
            default: dayjs.tz().format("YYYY-MM-DD"),
        },
        client: {
            type: String,
            default: "std",
        },
    },
    data() {
        return {
            luckyList: [],
        };
    },
    watch: {
        date: {
            immediate: true,
            handler() {
                if (this.client === "std") this.loadPetLucky();
            },
        },
    },
    methods: {
        // 获取福缘宠物
        loadPetLucky: function () {
            getPetLucky().then((res) => {
                let data = res.data.std;
                let dateIndex = dayjs.tz(this.date).format("MDD");
                let ids = data[dateIndex];
                getPets(ids).then((res) => {
                    this.luckyList = res.data.list;
                });
            });
        },
        // 前往宠物单页
        getPetLink(id) {
            return `/pet/${id}`;
        },
    },
};
</script>
