<template>
    <div class="m-simple-daily is-disabled">
        <div class="m-simple-daily-item" :class="item.isDone && 'is-done'" v-for="item in daily" :key="item.type">
            <div class="u-name">
                <span>{{ item.type }}</span>
            </div>
            <div v-if="~~item.name" class="u-times">
                <div class="u-time" :class="index <= ~~item.name && 'is-active'" v-for="index in 3" :key="index">
                    {{ index }}
                </div>
            </div>
            <div v-else class="u-desc">{{ item.name }}</div>
            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:class="'u-icon'" v-bind:svg-inline="''" :fill="item.isDone ? '#fff' : '#949494'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M8.6 11.8L6.425 9.625a.918.918 0 00-.675-.275c-.267 0-.5.1-.7.3a.948.948 0 00-.275.7c0 .283.092.517.275.7L7.9 13.9a.948.948 0 00.7.275.948.948 0 00.7-.275l5.675-5.675a.918.918 0 00.275-.675c0-.267-.1-.5-.3-.7a.948.948 0 00-.7-.275.948.948 0 00-.7.275L8.6 11.8zM10 20a9.733 9.733 0 01-3.9-.788 10.092 10.092 0 01-3.175-2.137c-.9-.9-1.612-1.958-2.137-3.175A9.732 9.732 0 010 10c0-1.383.263-2.683.788-3.9a10.092 10.092 0 012.137-3.175c.9-.9 1.958-1.613 3.175-2.138A9.743 9.743 0 0110 0c1.383 0 2.683.262 3.9.787a10.105 10.105 0 013.175 2.138c.9.9 1.612 1.958 2.137 3.175A9.733 9.733 0 0120 10a9.733 9.733 0 01-.788 3.9 10.092 10.092 0 01-2.137 3.175c-.9.9-1.958 1.612-3.175 2.137A9.733 9.733 0 0110 20z" fill="tranparent"/></svg>
        </div>
        <el-tooltip effect="dark" :content="(meirentu && meirentu.desc) || '无'" placement="bottom">
            <div class="m-simple-daily-item">
                <div class="u-name">
                    <span>美人图</span>
                    <span class="u-week">{{ week }}</span>
                </div>
                <span class="u-desc">{{ (meirentu && meirentu.name) || "今日暂无画像" }}</span>
            </div>
        </el-tooltip>
    </div>
</template>

<script>
import { getMeirentu } from "@/service/gonggao";
import { getDaily } from "@/service/gonggao";
import dayjs from "@/plugins/day";
export default {
    name: "SimpleDaily",
    data() {
        return {
            defaultDaily: [
                {
                    type: "据点贸易",
                    name: "2",
                },
                {
                    type: "茶馆",
                    name: "晟江",
                },
                // {
                //     type: "家园送信",
                //     name: "广陵邑/枫叶泊/九寨沟",
                // },
            ],
            daily: [],
            meirentu: {},
        };
    },
    computed: {
        date() {
            // 当7点以前，请求前面一天的日常 当7~24点，请求当天的日常
            const hour = dayjs.tz().get("hours");
            return 0 <= hour && hour < 7
                ? dayjs.tz().subtract(1, "day").format("YYYY-MM-DD")
                : dayjs.tz().format("YYYY-MM-DD");
        },
        client() {
            return this.$store.state.client;
        },
        isCurrentWeek() {
            let week = dayjs.tz(this.date).isoWeek();
            let currentWeek = dayjs.tz().isoWeek();
            return week === currentWeek;
        },
        server() {
            return this.$store.state.server;
        },
        week() {
            var datas = dayjs.tz(this.meirentu?.date || new Date()).day();
            var week = ["日", "一", "二", "三", "四", "五", "六"];
            return "周" + week[datas];
        },
    },
    watch: {
        server: {
            handler(val) {
                val && this.loadMeirentu();
            },
        },
    },
    methods: {
        loadDaily() {
            getDaily({ date: this.date }).then((res) => {
                let list = res.data.data || [];
                this.daily = list
                    .map((item) => {
                        return {
                            type: item.task_type,
                            name: item.activity_name,
                        };
                    })
                    .concat(this.defaultDaily);
            });
        },
        // 获取美人图
        loadMeirentu() {
            getMeirentu(this.server).then((res) => {
                this.meirentu = res.data?.data || {};
            });
        },
    },
    mounted() {
        this.loadMeirentu();
        this.loadDaily();
    },
};
</script>
